* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  margin: 0;
  overflow-x: hidden;
  position: relative;
}

img {
  max-width: 100%;
  height: 100%;
  object-position: center;
  object-fit: cover;
}

#cms-cookie-consent {
  font-size: 1.65em;
}

body {
  font-family: "Roboto", sans-serif;
  font-size: 50% !important;
}
@media (min-width: 1200px) {
  body {
    font-size: 62.5% !important;
  }
}

h1 {
  font-size: 4em;
  margin-bottom: 0.5em;
  font-weight: 400;
}

h2 {
  font-size: 3em;
  line-height: 1.3;
  margin-bottom: 0.5em;
  font-weight: 400;
}

h3 {
  margin-bottom: 0.5em;
  line-height: 1.1;
  font-size: 2em;
}

p {
  font-size: 1.5em;
  margin-bottom: 0.5em;
  line-height: 1.3;
}

a {
  text-decoration: none;
  color: inherit;
}

.full-height img {
  max-height: fit-content !important;
}

.editor-styles-wrapper {
  padding-top: 2rem;
}

.wp-block-columns {
  padding-top: 1em;
}

.coming-soon {
  width: 100%;
  text-align: center;
  margin-top: 35%;
}